.recs-list .list-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
  gap: 1rem;
}
.recs-list .list-header .list-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}
.recs-list .list-header .list-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.recs-list .list-header .list-info .item-count {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.recs-list .list-header .list-info .page-size-selector {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.recs-list .list-header .list-info .page-size-selector .page-size-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.recs-list .list-header .list-info .page-size-selector .page-size-select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  color: #374151;
  background: white;
  cursor: pointer;
}
.recs-list .list-header .list-info .page-size-selector .page-size-select:focus {
  outline: none;
  border-color: #8b5cf6;
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.1);
}
.recs-list .loading-state {
  display: grid;
  gap: 1.5rem;
}
.recs-list .loading-state .skeleton-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
}
.recs-list .loading-state .skeleton-card .skeleton-header {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}
.recs-list .loading-state .skeleton-card .skeleton-header .skeleton-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.75rem;
}
.recs-list .loading-state .skeleton-card .skeleton-header .skeleton-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.recs-list .loading-state .skeleton-card .skeleton-header .skeleton-content .skeleton-line {
  height: 1rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.25rem;
}
.recs-list .loading-state .skeleton-card .skeleton-header .skeleton-content .skeleton-line.w-3-4 {
  width: 75%;
}
.recs-list .loading-state .skeleton-card .skeleton-header .skeleton-content .skeleton-line.w-1-2 {
  width: 50%;
}
.recs-list .loading-state .skeleton-card .skeleton-body {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.recs-list .loading-state .skeleton-card .skeleton-body .skeleton-line {
  height: 0.875rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.25rem;
}
.recs-list .loading-state .skeleton-card .skeleton-body .skeleton-line.w-full {
  width: 100%;
}
.recs-list .loading-state .skeleton-card .skeleton-body .skeleton-line.w-2-3 {
  width: 66.666667%;
}
.recs-list .error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 1.5rem;
  text-align: center;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
}
.recs-list .error-state .error-icon {
  color: #ef4444;
  margin-bottom: 1rem;
}
.recs-list .error-state .error-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.recs-list .error-state .error-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 1.5rem 0;
  line-height: 1.5;
}
.recs-list .error-state .retry-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.recs-list .error-state .retry-button:hover {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
  transform: translateY(-1px);
}
.recs-list .error-state .retry-button:active {
  transform: translateY(0);
}
.recs-list .error-state .retry-button svg {
  width: 1rem;
  height: 1rem;
}
.recs-list .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 1.5rem;
  text-align: center;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
}
.recs-list .empty-state .empty-icon {
  color: #d1d5db;
  margin-bottom: 1rem;
}
.recs-list .empty-state .empty-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.recs-list .empty-state .empty-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 1.5rem 0;
  line-height: 1.5;
  max-width: 24rem;
}
.recs-list .empty-state .generate-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.recs-list .empty-state .generate-button:hover {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
  transform: translateY(-1px);
}
.recs-list .empty-state .generate-button:active {
  transform: translateY(0);
}
.recs-list .empty-state .generate-button svg {
  width: 1rem;
  height: 1rem;
}
.recs-list .recommendations-grid {
  display: grid;
  gap: 1.5rem;
}
.recs-list .pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  padding: 1rem;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
}
.recs-list .pagination .pagination-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}
.recs-list .pagination .pagination-btn:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}
.recs-list .pagination .pagination-btn:active:not(:disabled) {
  transform: translateY(0);
}
.recs-list .pagination .pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}
.recs-list .pagination .pagination-btn svg {
  width: 1rem;
  height: 1rem;
}
.recs-list .pagination .pagination-info {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
@media (max-width: 768px) {
  .recs-list .list-header {
    flex-direction: column;
    align-items: stretch;
    text-align: center;
  }
  .recs-list .list-header .list-info {
    justify-content: center;
    flex-wrap: wrap;
  }
  .recs-list .loading-state .skeleton-card {
    padding: 1rem;
  }
  .recs-list .loading-state .skeleton-card .skeleton-header .skeleton-icon {
    width: 2rem;
    height: 2rem;
  }
  .recs-list .error-state,
  .recs-list .empty-state {
    padding: 2rem 1rem;
  }
  .recs-list .error-state .error-title,
  .recs-list .error-state .empty-title,
  .recs-list .empty-state .error-title,
  .recs-list .empty-state .empty-title {
    font-size: 1.125rem;
  }
  .recs-list .error-state .retry-button,
  .recs-list .error-state .generate-button,
  .recs-list .empty-state .retry-button,
  .recs-list .empty-state .generate-button {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
  .recs-list .pagination {
    flex-direction: column;
    gap: 0.75rem;
  }
  .recs-list .pagination .pagination-btn {
    width: 100%;
    justify-content: center;
  }
}
